import React from 'react';
import { StyleSheet, Text, View, Image, Button, Alert } from 'react-native';

export default function App() {

  let title='Get response from server'

  let mainImg = {
    uri: 'https://fontmeme.com/temporary/760ffcebb679896e3b0f872f59338cb2.png'
  };

  function componentDidMount() {
    const response = {

      statusCode: 200,
  
      headers: {
        'Access-Control-Allow-Origin': 'http://localhost:19006',
        'Access-Control-Allow-Credentials': false,
      },
      body: 
        fetch('http://ec2-52-14-234-1.us-east-2.compute.amazonaws.com/api', {
          method: 'GET'
        })
        .then((response) => response.json())
        .then((responseJson) => {
            console.log(JSON.stringify(responseJson));
            Alert.alert(
              "Good response", 
              JSON.stringify(responseJson) + 'was returned',
              [
                { text: 'OK'}
              ]
            )
        })
        .catch((error) => {
            console.error(error);
        }),
    };
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#EEEEEE',
    alignItems: 'center',
    justifyContent: 'center',
  },

  image: {
    width: 350, 
    height: 300,
    resizeMode: 'contain',
  },
});

return (
  <View style={styles.container}>
    <Image source={mainImg} style={styles.image}/>
    <Button title={title} onPress={()=>componentDidMount()}/>
  </View>
);
}